@import "_mixin";
@import "_variables";
// Стили
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,400;0,700;1,600&display=swap');
* {
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;
    box-sizing: border-box;
    outline: none;
}

.multiselect {
    &__tag,
    &__single {
        padding: 5px;
        margin-right: 5px;
        border-radius: 15px;
    }
    &__content {
        padding: 0;
        margin: 10px 0 0 0;
    }
    &__element {
        padding-bottom: 5px;
        margin-bottom: 5px;
        border-bottom: 2px solid black;
        &:hover {
            color: red;
            border-bottom: 2px solid red;
        }
    }
}

body,
html,
fieldset {
    margin: 0;
    padding: 0;
    border: none;
}

body {
    background-color: rgba(255, 238, 238, 0.4);
    padding: 30px 0;
}

.visually-hidden {
    position: absolute !important;
    clip: rect(1px 1px 1px 1px);
    clip: rect(1px, 1px, 1px, 1px);
    padding: 0 !important;
    border: 0 !important;
    height: 1px !important;
    width: 1px !important;
    overflow: hidden;
}

ul {
    list-style: none;
}

p {
    font-family: inherit;
}

input,
textarea,
button,
button {
    cursor: pointer;
    border: none;
    outline: none;
}

address {
    font-style: normal;
}

a,
a:visited {
    text-decoration: none;
    &:focus {
        border: none;
        outline: none;
    }
}

button,
textarea {
    cursor: pointer;
    border: none;
    outline: none;
    background-color: rgba(0, 0, 0, 0);
}

h1,
h2,
h3,
h4,
h5,
h6 {
    padding: 0;
    margin: 0;
    font-weight: 700;
}

.error {
    color: red;
}

.success {
    color: green;
}

